import {h} from 'preact';
import cleanSku from '../../../util/cleanSku';
import SizedImage from '../../common/SizedImage';
import Icon from '../../common/Icon';
import Item from './Item';

const cls = {
    root: 'flex py-2 px-4 border-b border-solid border-grey-lighter',
    left: 'flex-0 w-24',
    right: 'flex-1 w-px pl-2',
    thumbnail: 'block img-ratio-4x3 border border-solid border-grey-lighter bg-white',
    title: 'ellipsis leading-normal my-0 text-sm',
    detail: 'my-0 text-sm',
    after: 'pl-2 self-center'
};

const ItemProduct = ( {sku, longName, shortName, indicator, className = '', ...props} ) => {
    return (
        <Item className={[cls.root, className].join( ' ' )}
              data-quick-view
              href={`https://store.ultradent.com/?items=${ sku }`}
              {...props}>
            <div className={cls.left}>
                <SizedImage type="sku" src={sku} alt={longName} className={cls.thumbnail}/>
            </div>
            <div className={cls.right}>
                <h4 className={cls.title}>{longName}</h4>
                <p className={cls.detail}>{indicator} #{cleanSku( sku )}</p>
            </div>
            <div className={cls.after}>
                <Icon icon="shopping-cart-add" className="text-xl"/>
            </div>
        </Item>
    );
};

export default ItemProduct;



