import './modules/hotModule';
import {h, render} from 'preact';
import {SKU_PATTERN} from './upi/constants';
import Search from './components/search/Search';
import ItemProduct from './components/search/list/ItemProduct';
import Item from './components/search/list/Item';
import Icon from './components/common/Icon';
import CatalogService from './services/CatalogService';
import OrdersService from './services/OrdersService';

// todo -> Reimplement tracking: Tracking should be handled outside of this component
// through a public API expose from Search component, eg, onSearch, onEnter, onResultSelect etc
// const searchResultTypes = {
//     SEARCH_CLICK: 'Search icon Click',
//     SEARCH_ENTER: 'Show all ENTER Key',
//     SHOW_ALL_CLICK: 'Show all Click',
//     PAST_PURCHASE: 'Past Purchase',
//     PRODUCT: 'Product',
//     SKU: 'SKU'
// };
//
// function trackSearchResultType ( searchType ) {
//     upi.dataLayer.searchBar.setResultType( searchType ).send();
// }

const SearchSuggestions = {
    init () {
        const getCatalog = CatalogService.fetchStatic();
        const getRecentPurchases = new Promise( ( resolve ) => {
            // return empty Array if user not logged in or request fails
            return OrdersService.getPastPurchases( 32 ).then(
                data => resolve( data ),
                err => resolve( {Items: []} )
            );
        } );

        Promise.all( [getCatalog, getRecentPurchases] ).then( ( [catalog, recentPurchases] ) => {

            if ( !catalog.Items ) {
                return false;
            }

            const products = this.reduceProductList( catalog.Items );
            const searchBoxWrapper = this.searchBoxWrapper = document.getElementById( 'search-bar-wrapper--header' );

            if ( searchBoxWrapper ) {
                const inp = searchBoxWrapper.querySelector( 'input[type="text"]' );
                const recentPurchasesSkus = recentPurchases.Items.map( i => i.Sku );

                const skuRenderer = ( {Sku, KitName} ) =>
                    <ItemProduct as="a" sku={Sku} longName={KitName}/>;

                const itemRenderer = ( {Name, GroupId} ) =>
                    <Item as="a"
                        // todo -> productPageUrl service deprecated - need to get from graphQL api
                          href={upi.conf.svc.get_product_page( GroupId )}>
                        {Name}
                        <Icon icon="arrow-right" className="text-xl"/>
                    </Item>;

                searchBoxWrapper.innerHTML = '';

                render(
                    <Search
                        initialQuery={inp && inp.value || ''}
                        onResultSelect={( evt, result ) => {
                            const el = $( `#search-result-item-${ result.index } > a` );
                            // only handle these actions if event was <enter> key
                            if ( evt.which === 13 ) {
                                switch ( result.queryGroup.groupName ) {
                                    case 'Buy it again':
                                    case 'SKUs':
                                        el.click();
                                        break;

                                    default:
                                        window.location = el.attr( 'href' );

                                }
                            }
                        }}
                        queryGroup={[
                            {
                                groupName: 'Buy it again',
                                list: recentPurchases.Items,
                                queryProps: ['KitName', 'Sku'],
                                renderer: skuRenderer
                            },
                            {
                                groupName: 'Products',
                                list: Array.from( products.entries() ).map( i => i[1] ),
                                queryProps: ['Name'],
                                renderer: itemRenderer
                            },
                            {
                                groupName: 'SKUs',
                                list: catalog.Items,
                                queryProps: ['KitName', 'Sku'],
                                renderer: skuRenderer,
                                filter: item => recentPurchasesSkus.indexOf( item.Sku ) === -1,
                                hideIf: ( query, resultList ) =>
                                    !SKU_PATTERN.test( query ) && resultList[1].length > 5
                            }
                        ]}

                    />,
                    searchBoxWrapper
                );

            }
            else {
                console.warn( 'Could not mount <Search/> component; Root node #SearchBox not found' );
            }
        } );
    },

    reduceProductList ( catalog ) {
        const map = new Map();
        catalog.forEach( i => map.set( i.ProductName, {Name: i.ProductName, GroupId: i.Sku} ) );
        return map;
    }
};

export default SearchSuggestions;





