import ServiceProvider from '../modules/core/ServiceProvider';

const svc = new ServiceProvider( {
    baseURL: upi.conf.svc.ECOMMERCE_API,
    cache: true
} );

const CatalogService = {

    fetch() {
        return svc.get( '/getCatalog', {cacheForUser: true} )
            .then( ServiceProvider.responseData );
    },

    /**
     * Returns static version of catalog hardcoded to default pricelist
     */
    fetchStatic() {
        return svc.get( '/js/static-data/catalog.jason',
            {
                baseURL: upi.paths.ORIGIN
            } )
            .then( ServiceProvider.responseData );
    }

};

export default CatalogService;














