import {h, Component} from 'preact';
import assign from 'object-assign';
import invoke from 'lodash.invoke';
import flatten from 'lodash.flatten';
import SearchField from './SearchField';
import SearchResults from './SearchResults';
import {createSearchContext} from 'upi-fuzzy-search';
import SearchResultItem from './SearchResultItem';
import List from './list/List';
import GroupLabel from './list/GroupLabel';
import shallowCompare from 'preact-shallow-compare';

const defaultProps = {
    accountGuid: null,
    resultLimit: 7
};

class Search extends Component {

    results = [];
    queryGroups = [];

    constructor ( props ) {
        super( props );

        const queryGroups = Array.isArray( props.queryGroup ) ? props.queryGroup : [props.queryGroup];

        // create a list of searchContexts each with their own data sets that can later be queried
        this.queryGroups = queryGroups.map( grp => {
            const {list, queryProps, resultLimit = props.resultLimit} = grp;
            const ctx = createSearchContext( list, queryProps, resultLimit );

            return {...grp, query: ctx.query};
        } );

        this.state = {
            selectedIndex: -1,
            isActive: false,
            query: props.initialQuery || ''
        }
    }

    shouldComponentUpdate ( nextProps, nextState ) {
        return shallowCompare( this, nextProps, nextState );
    }

    componentDidMount () {
        document.addEventListener( 'click', e => {
            let target = e.target;
            // click occurred inside the search component or a page modal -> remain active
            while (
                target
                && target !== this.wrapper
                && !target.id.match( 'modal|header-outer' )
                && target.nodeName.toLowerCase() !== 'html' ) {
                target = target.parentNode;
            }
            if ( !target || target.nodeName.toLowerCase() === 'html' ) {
                this.setState( {isActive: false} );
            }
        } );
    }

    validateQuery () {
        return this.state.query.trim() !== '';
    }

    setSelectionIndex = ( index ) => {
        // force index boundaries from 0 - maxLength
        const selectedIndex = Math.min( Math.max( index, -1 ), this.visibleResultCount - 1 );
        this.setState( {selectedIndex} );
    };

    selectItemOnEnter = ( evt ) => {
        evt.preventDefault();
        const index = this.state.selectedIndex;
        if ( index === -1 ) {
            return;
        }

        this.handleResultSelect( evt, flatten( this.results )[index] );
    };

    handleResultClick = ( evt, {index} ) => {
        this.handleResultSelect( evt, flatten( this.results )[index] );
    };

    handleResultHover = ( evt, {index} ) => {
        this.setSelectionIndex( index );
    };

    handleResultSelect = ( evt, result ) => {
        invoke( this.props, 'onResultSelect', evt, result );
    };

    onInput = value => {
        this.onSearch( value );
    };

    onClear = () => {
        this.setState( {query: ''} );
    };

    onFocus = () => {
        this.setState( {isActive: true} );
        this.onSearch( this.state.query );
    };

    onKeyUp = keycode => {
        this.setState( {isActive: keycode !== 27} );
    };

    onKeyDown = keycode => {
        switch ( keycode ) {
            case 38:
                this.setSelectionIndex( this.state.selectedIndex - 1 );
                break;
            case 40:
                this.setSelectionIndex( this.state.selectedIndex + 1 );
                break;

            default:
            // fall through
        }
    };

    onSearch = query => {
        // reset selectedIndex anytime a query changes
        this.setSelectionIndex( -1 );

        if ( this.state.isLoading ) {
            return false;
        }

        this.setState( {query} );
    };

    getQueriedResults = query => {
        let index = 0;

        return this.queryGroups.map( ctx => {
            let results = ctx.query( query );
            // filter resultSet if necessary
            results = ctx.filter ? results.filter( ctx.filter ) : results;

            // apply index & metadata to each result
            return results.map( data => assign( {index: index++}, data, {queryGroup: ctx} ) );
        } )
    };

    renderResults ( resultList ) {
        const {query, selectedIndex} = this.state;
        let count = 0;
        this.visibleResultCount = count++;

        return this.queryGroups.map( ( queryGroup, i ) => {
            const {groupName, renderer, hideIf} = queryGroup;
            // invoke "hideIf" predicate to determine whether to display with @params query, currentResults
            if ( hideIf && hideIf( query, resultList ) ) {
                return null;
            }

            if ( resultList[i].length > 0 ) {
                return (
                    <List>
                        <GroupLabel>{groupName}</GroupLabel>
                        {resultList[i].map( result => {
                                this.visibleResultCount = count++;
                                return <SearchResultItem
                                    index={result.index}
                                    key={`SearchResultItem-${result.index}`}
                                    onResultHover={this.handleResultHover}
                                    onResultClick={this.handleResultClick}
                                    active={selectedIndex === result.index}>
                                    {renderer( result )}
                                </SearchResultItem>
                            }
                        )}
                    </List>
                )
            }
        } );
    }

    renderNoResults () {
        return (
            <div className={'flex justify-center self-center text-xl py-8 px-4 bg-white'}>
                No Results
            </div>
        );

    }

    renderResultContent ( query ) {
        this.results = this.getQueriedResults( query );

        if ( !flatten( this.results ).length ) {
            return this.renderNoResults();
        }

        return this.renderResults( this.results );
    }

    renderResultsPane ( query, isActive ) {
        if ( query.length <= 2 || !isActive ) {
            return;
        }

        const resultContent = this.renderResultContent( query );
        if ( !resultContent ) {
            return;
        }

        return <SearchResults>{resultContent}</SearchResults>
    }

    render () {
        if ( this.state.isLoading ) {
            // todo: load preloading screen
            return null;
        }

        // console.warn( '[Search] Render' );

        const {query, isActive} = this.state;

        return (
            <div ref={ref => this.wrapper = ref} className="w-full">
                <SearchField
                    query={query}
                    onClear={this.onClear}
                    onInput={this.onInput}
                    onEnter={this.selectItemOnEnter}
                    onKeyDown={this.onKeyDown}
                    onKeyUp={this.onKeyUp}
                    onFocus={this.onFocus}
                />
                {this.renderResultsPane( query, isActive )}
            </div>
        );
    }
}

Search.defaultProps = defaultProps;

export default Search;






