import {h} from 'preact';

const cls = 'flex justify-between items-center p-4 border-b border-solid border-grey-lighter';
const ListItemLink = ( {as, className = '', children, ...props} ) => {
    const Element = as || 'div';

    // todo -> what is data-navigate-to
    return (
        <Element
            className={[cls, className].join( ' ' )}
            {...props}>
            {children}
        </Element>
    );
};

export default ListItemLink;



