import {h, Component} from 'preact';
import invoke from 'lodash.invoke';
import equal from 'deep-equal';

/**
 * SearchResultItem -> wrapper component to display current selected index mouse and keyboard interactions
 * @param active
 * @param children
 * @return {*}
 * @constructor
 */
class SearchResultItem extends Component {

    shouldComponentUpdate ( nextProps ) {
        return !equal( nextProps, this.props );
    }

    handleMouseEnter = evt => {
        invoke( this.props, 'onResultHover', evt, this.props )
    };

    handleClick = evt => {
        invoke( this.props, 'onResultClick', evt, this.props )
    };

    render () {
        const {index, active, children, ...rest} = this.props;

        // console.warn( '[SearchResultItem] Render' );

        return (
            <div id={`search-result-item-${ index }`}
                 onMouseEnter={this.handleMouseEnter}
                 onClick={this.handleClick}
                 className={active ? 'bg-grey-lighter link-1' : ''}
                 {...rest}>
                {children}
            </div>
        );
    }
}

export default SearchResultItem;



