import {h, Component} from 'preact';

class SearchResults extends Component {
    render () {
        return (
            <div
                className="search-results absolute flex flex-col pb-12 md:pb-0 bg-grey-lightest w-full overflow-auto shadow-md">
                <div className="search-results-list overflow-y-scroll scrolling-touch">
                    {this.props.children}
                </div>
            </div>
        );
    }
}

export default SearchResults;



