import {h, Component} from 'preact';
import invoke from 'lodash.invoke';
import Icon from '../common/Icon';
import shallowCompare from 'preact-shallow-compare';

class SearchField extends Component {

    static defaultProps = {
        placeholder: 'Keyword / Item#'
    };

    shouldComponentUpdate ( nextProps, nextState ) {
        return shallowCompare( this, nextProps, nextState );
    }

    onInput = e => {
        invoke( this.props, 'onInput', e.target.value, e );
    };
    onKeyPress = e => {
        invoke( this.props, 'onKeyPress', e.which, e );
    };
    onKeyDown = e => {
        invoke( this.props, 'onKeyDown', e.which, e );

        if ( e.which === 13 ) {
            invoke( this.props, 'onEnter', e );
        }
    };
    onKeyUp = e => {
        invoke( this.props, 'onKeyUp', e.which, e );
    };
    onFocus = e => {
        invoke( this.props, 'onFocus', e );
    };
    onClear = e => {
        this.input.focus();
        invoke( this.props, 'onClear', e );
    };

    render () {
        const {query, placeholder} = this.props;

        // console.warn( '[SearchField] Render' );

        return (
            <div id="search-bar--header"
                 className="flex w-full lg:flex-1 p-2 md:p-0 border-b border-solid border-grey-light md:border-none">
                    <span className="relative flex-1 m-0">
                        <input
                            ref={ref => this.input = ref}
                            className="w-full h-full rounded-r-none rounded-l"
                            type="text"
                            id="search-query--header"
                            value={query}
                            onInput={this.onInput}
                            onKeyPress={this.onKeyPress}
                            onKeyDown={this.onKeyDown}
                            onKeyUp={this.onKeyUp}
                            onFocus={this.onFocus}
                            placeholder={placeholder}
                            autocomplete="off"
                            autocapitalize="off"
                            autocorrect="off"/>

                        {query.length > 0 &&
                        <span
                            onClick={this.onClear}
                            className="cursor-pointer inline-block absolute pin-t pin-r p-2 w-8 text-center text-lg">&times;</span>
                        }
                    </span>
                <span data-action="search-bar-toggle--mobile"
                      className="md:hidden px-2 self-center text-lg text-sky-blue">Cancel</span>
                <button
                    type="button"
                    id="search-query-submit--header"
                    className="hidden md:block m-0 px-2 rounded-l-none rounded-r"
                    onClick={this.handleClick}>
                    <Icon icon="search" className="text-xl flex items-center"/>
                    <span className="a11y-only">Search</span>
                </button>
            </div>
        );
    }
}

export default SearchField;



