import {h} from 'preact';

// todo -> import from global config
const ICON_PATH = '/svg/ui-defs.svg';

const Icon = ( {icon, className = '', ...props} ) => {

    const classNames = [`svgi-${ icon }`, className].join( ' ' );

    // uses SPAN wrapper for click handlers because event handling is more consistent cross-browser than svg
    return (
        <span {...props}>
            <svg className={classNames}>
                <use xlinkHref={`${ ICON_PATH }#icon-${ icon }`}></use>
            </svg>
        </span>
    );
};

export default Icon;



